<template>
  <modal :show="show" @close="closeModal()">
    <template #title>
      {{ event ? 'Appointment Detail' : 'Add New Appointment' }}
    </template>
    <template #content>

      <form @submit.prevent="submit($event)">
        <div class="px-6 py-4">
          <div class="mt-2">
            <label class="block font-medium text-sm text-gray-700" for="property_id">Property</label>
            <vSelect id="property_id" v-model="form.property_id" :disabled="!isEditable" :options="properties"
                     :reduce="property => property.id" class="mt-1 block w-full" label="name"></vSelect>
            <InputError :message="errors?.property_id"/>
          </div>

          <div class="mt-2">
            <label class="block font-medium text-sm text-gray-700" for="description">Description</label>
            <VueTrix id="description" v-model="form.description" class="mt-1"/>
            <InputError :message="errors?.description"/>
          </div>

          <div class="grid grid-cols-2 lg:gap-x-4">
            <div class="col-span-2 lg:col-span-1 mt-2">
              <label class="block font-medium text-sm text-gray-700" for="start">Start</label>
              <DateTimePicker id="start" v-model="form.start" :disabled="!isEditable" class="mt-1 block w-full"/>
              <InputError :message="errors?.start"/>
            </div>

            <div class="col-span-2 lg:col-span-1 mt-2">
              <label class="block font-medium text-sm text-gray-700" for="end">End</label>
              <DateTimePicker id="end" v-model="form.end" :disabled="!isEditable" class="mt-1 block w-full"/>
              <InputError :message="errors?.end"/>
            </div>
          </div>

          <div class="col-span-2 lg:col-span-1 mt-2">
            <label class="block font-medium text-sm text-gray-700" for="summary">Summary</label>
            <input id="summary" v-model="form.summary" :class="{ 'bg-gray-100': !isEditable }" :readonly="!isEditable"
                   class="form-input mt-1 block w-full" type="text"/>
            <InputError :message="errors?.summary"/>
          </div>

          <div v-if="event && isEditable" class="col-span-2 mt-2">
            <FilePond ref="pondEl" :allow-multiple="true"
                      accepted-file-types="image/jpg, image/jpeg, image/png"
                      labelIdle='Drag & Drop your files or <span class="filepond--label-action">Browse</span>' max-file-size="2MB" name="image"
                      @init="handleFilePondInit" @processfile="handleProcessedFile"/>
          </div>

          <div v-if="event && event.extendedProps.attachments.length > 0 && event.extendedProps.completed_at !== null">
            <h3 class="font-semibold text-gray-800 mt-2">Attachments</h3>
            <div class="grid grid-cols-3 gap-2 justify-evenly mt-4">
              <div v-for="attachment in event.extendedProps.attachments" :key="attachment.id">
                <img :alt="attachment.file_name" :src="attachment.url" class="w-full h-full object-cover"/>
              </div>
            </div>
          </div>

        </div>
        <div class="px-6 py-4 bg-gray-100 text-right">
          <button :disabled="processing" class="btn btn-default" type="button" @click="closeModal()">Close</button>
          <template v-if="event && event.extendedProps.uid === null && event.extendedProps.completed_at === null">
            <button v-if="hasAnyPermission('delete-appointments')" :disabled="processing" class="btn btn-danger ml-2"
                    type="button"
                    @click="deleteAppointment()">Delete
            </button>
            <button v-if="hasAnyPermission('update-appointments')" :disabled="processing" class="btn btn-success ml-2"
                    type="button"
                    @click="markAppointmentCompleted()">Mark Completed
            </button>
          </template>
          <button v-if="isEditable && hasAnyPermission('create-appointments')" :disabled="processing"
                  class="ml-2 btn btn-primary" type="submit">Save
          </button>
        </div>
      </form>
    </template>
  </modal>
</template>

<script setup>
import Modal from '@/Components/Common/Modal'
import {inject, onMounted, onUnmounted, reactive, ref} from 'vue'
import DateTimePicker from '@/Components/Common/DateTimePicker'
import InputError from '@/Components/Common/InputError'
import VueTrix from '@/Components/Common/VueTrix'
import vueFilePond, {setOptions} from "vue-filepond"
import "filepond/dist/filepond.min.css"
import FilePondPluginFileValidateType from "filepond-plugin-file-validate-type"
import FilePondPluginFileValidateSize from "filepond-plugin-file-validate-size"
import FilePondPluginImagePreview from 'filepond-plugin-image-preview'
import 'filepond-plugin-image-preview/dist/filepond-plugin-image-preview.css'
import axios from 'axios'
import findIndex from 'lodash/findIndex'
import moment from 'moment'
import vSelect from 'vue-select'

const show = ref(false)
const emitter = inject('emitter')
const hasAnyPermission = inject('hasAnyPermission')
const event = ref(null)
const properties = ref([])
const isEditable = ref(true)
const processing = ref(false)
const attachments = ref([])
const errors = ref({})

const FilePond = vueFilePond(
  FilePondPluginFileValidateType,
  FilePondPluginFileValidateSize,
  FilePondPluginImagePreview
)

const form = reactive({
  property_id: null,
  description: '',
  start: '',
  end: '',
  summary: ''
})

let refreshEventsHook = () => {
}

let serverMessage = {};
const pondEl = ref(null)
const handleFilePondInit = () => {
}
const handleProcessedFile = (error, file) => {
  if (!error) {
    refreshEventsHook()
    attachments.value.push(JSON.parse(file.serverId))
  }
}

const closeModal = () => {
  show.value = false
}

const swal = inject('swal')
const deleteAppointment = () => {
  if (event.value && event.value.extendedProps.uid === null && isEditable.value) {
    swal.fire({
      title: "",
      html: "<h1 class='text-lg text-gray-800 mb-1'>Delete Appointment</h1><p class='text-base'>Are you sure want to delete this record?</p>",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: "Delete",
      customClass: {
        confirmButton: 'danger'
      }
    }).then((result) => {
      if (result.value) {
        showWaitDialog()
        axios.delete(route('appointments.destroy', event.value.id))
          .then(response => {
            hideWaitDialog()
            closeModal()
            refreshEventsHook()
            toast.fire({
              icon: 'success',
              title: response.data.message
            })
          }).catch(err => {
          hideWaitDialog()
          toast.fire({
            icon: 'error',
            title: err.response.data.error
          })
        })
      }
    })
  }
}

const markAppointmentCompleted = () => {
  if (event.value && event.value.extendedProps.uid === null && isEditable.value) {
    swal.fire({
      title: "",
      html: "<h1 class='text-lg text-gray-800 mb-1'>Mark Appointment Completed</h1><p class='text-base'>Are you sure want to delete this record?</p>",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: "Mark Completed",
      customClass: {
        confirmButton: 'success'
      }
    }).then((result) => {
      if (result.value) {
        showWaitDialog()
        axios.put(route('appointments.mark-completed', event.value.id))
          .then(response => {
            hideWaitDialog()
            closeModal()
            refreshEventsHook()
            toast.fire({
              icon: 'success',
              title: response.data.message
            })
          }).catch(err => {
          hideWaitDialog()
          toast.fire({
            icon: 'error',
            title: err.response.data.error
          })
        })
      }
    })
  }
}

const submit = (evt) => {
  evt.preventDefault()
  if (event.value) {
    processing.value = true
    const payload = {...form}
    const start = typeof form.start === 'string' ? form.start.replace(/-/g, '/') : form.start
    payload.start = moment(new Date(start)).format('YYYY-MM-DD HH:mm')
    const end = typeof form.end === 'string' ? form.end.replace(/-/g, '/') : form.end
    payload.end = moment(new Date(end)).format('YYYY-MM-DD HH:mm')
    axios.put(route('appointments.update', event.value.id), payload)
      .then(response => {
        processing.value = false
        closeModal()
        refreshEventsHook()
        toast.fire({
          icon: 'success',
          title: response.data.message
        })
      }).catch(err => {
      processing.value = false
      errors.value = err.response.data.errors
    })
  } else {
    processing.value = true
    const payload = {...form}
    payload.start = moment(new Date(form.start)).format('YYYY-MM-DD HH:mm')
    payload.end = moment(new Date(form.end)).format('YYYY-MM-DD HH:mm')
    axios.post(route('appointments.store'), payload)
      .then(response => {
        processing.value = false
        closeModal()
        refreshEventsHook()
        toast.fire({
          icon: 'success',
          title: response.data.message
        })
      }).catch(err => {
      processing.value = false
      if (err.response.status === 422) {
        errors.value = err.response.data.errors
      } else {
        toast.fire({
          icon: 'error',
          title: err.response.data.error
        })
      }
    })
  }
}

const showModal = (args) => {
  event.value = args.event ? args.event : null
  properties.value = args.properties
  refreshEventsHook = args.refreshEvents
  isEditable.value = true
  form.property_id = event.value ? event.value.extendedProps.property.id : ''
  form.description = event.value ? event.value.extendedProps.description : ''
  form.start = event.value ? event.value.start : (args?.start ? args.start : '')
  form.end = event.value ? event.value.end : ''
  form.summary = event.value ? event.value.extendedProps.summary : ''
  if (event.value) {
    attachments.value = event.value.extendedProps.attachments
    setOptions({
      files: event.value.extendedProps.attachments.map(file => {
        return {
          source: file.url,
          options: {type: 'local'}
        }
      }),
      server: {
        load: (source, load, error, progress, abort, headers) => {
          const req = new Request(source);
          fetch(req).then(function (response) {
            response.blob().then(function (img) {
              load(img)
            })
          })
        },
        process: {
          url: route('upload-image', event.value.id),
          onerror: (response) => {
            serverMessage = JSON.parse(response)
          },
          headers: {
            'X-CSRF-TOKEN': document.head.querySelector('meta[name="csrf_token"]').content
          }
        },
        revert: (uniqueId, load, error) => {
          const file = JSON.parse(uniqueId)
          const fileIndex = findIndex(attachments.value, {id: file.id})
          if (fileIndex === -1) {
            error('Invalid file url')
          }
          axios.delete(route('delete-image', file.id))
            .then((response) => {
              attachments.value.splice(fileIndex, 1)
              load()
              refreshEventsHook()
            }).catch((err) => {
            toast.fire({
              icon: 'error',
              title: err.response.data.error
            })
          })
        },
        remove: (source, load, error) => {
          const fileIndex = findIndex(attachments.value, {url: source})
          if (fileIndex === -1) {
            error('Invalid file url')
          }
          axios.delete(route('delete-image', attachments.value[fileIndex].id))
            .then((response) => {
              attachments.value.splice(fileIndex, 1)
              load()
            }).catch((err) => {
            console.log(err.response.data.message)
          })
        },
      },
      labelFileProcessingError: () => {
        return serverMessage.error
      }
    })
    if (event.value.end.getTime() <= new Date().getTime() || event.value.extendedProps.completed_at !== null) {
      isEditable.value = false
    }
  }
  // Show modal
  show.value = true

  if (!isEditable.value) {
    setTimeout(() => {
      const toolbarId = document.querySelector('trix-editor').getAttribute('toolbar')
      document.querySelector('trix-editor').setAttribute('contentEditable', false)
      document.querySelector('trix-editor').classList.add('bg-gray-100')
      document.getElementById(toolbarId).classList.add('hidden', 'bg-gray-100')
    }, 100)
  }
}

onMounted(() => {
  emitter.on('showEventModal', showModal)
})


onUnmounted(() => {
  emitter.off('showEventModal', showModal)
})

</script>
